import { Component } from '@angular/core';
import { NavController, Platform, App, Events } from 'ionic-angular';
import { LearnService } from '../../app/learn.service';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { MenuController } from 'ionic-angular';
@Component({
  selector: 'video-local',
  templateUrl: 'local.html'
})
export class VideoLocalPage {
  pages: string = "Apuntes";
  srcVideo: any = "";
  screen: boolean = false;
  constructor(public navCtrl: NavController, public platform: Platform, public service: LearnService, public screenOrientation: ScreenOrientation, public events: Events, public menuCtrl: MenuController, public app: App) {
    //Utilizado para detectar los cambios de orientacion del dispositivo
    this.screenOrientation.onChange().subscribe(
      () => {
        this.fullScreen()
      }
    );

    //Usado para recibir el evento enviado desde el modul con la url del video del modul descargado
    events.subscribe('Mod', (modul) => {
      if (modul != "") {
        this.srcVideo = modul;
      }
    });
  }

  //Utilizada para detectar la posicion y cambiar la position dependiendo de cual sea
  fullScreen() {
    if (this.screenOrientation.type == "portrait-primary") {
      this.screen = false;
    } 
    if (this.screenOrientation.type == "landscape-primary") {
      this.screen = true;
    }
    if (this.screenOrientation.type == "landscape-sencondary") {
      this.screen = true;
    }
  }

  fullScreenButton() {
    if (this.screenOrientation.type == "portrait-primary") {
      this.screen = true;
      this.lockScreenOrientation();
    }
    if (this.screenOrientation.type == "landscape-primary") {
      this.screen = false;
      this.unlockScreenOrientation();
    }
    if (this.screenOrientation.type == "landscape-secondary") {
      this.screen = false;
      this.unlockScreenOrientation();
    }
  }

  //Utilizada para bloquear la posicion del dispositivo cuando  esta en modo retrato a modo paisaje primaria o secundaria
  async lockScreenOrientation() {
    try {
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.LANDSCAPE);

    } catch (error) {
      console.error(error);
    }
  }

  //Utilizada para desbloquear la posicion del dispositivo cuando  esta en modo paisaje primaria o secundaria
  unlockScreenOrientation() {
    this.screenOrientation.unlock();
  }
  //Obtiene cual es la pagina anterior y la retorna
  backCourse() {
    this.app.goBack();
  }


}



