import { Component, ViewChild, NgZone } from '@angular/core';
import { Nav, NavController, NavParams, AlertController, Platform, Events } from 'ionic-angular';
import { LearnService } from '../../app/learn.service';
import { VideoLocalPage } from "../video/local";
import { Transfer, TransferObject } from '@ionic-native/transfer';
import { File } from '@ionic-native/file';
declare var cordova: any;

@Component({
  selector: 'page-modules',
  templateUrl: 'modules.html'

})
export class ModulesPage {
  @ViewChild(Nav) nav: Nav;
  rootPage: any = VideoLocalPage;
  showGroup = null;
  storageDirectory: string = '';
  loadProgress: any = 30;
  constructor(public navCtrl: NavController, public navParams: NavParams, public service: LearnService, public platform: Platform, public transfer: Transfer, public file: File, public alertCtrl: AlertController, public events: Events, public _zone: NgZone) {
    this.platform.ready().then(() => {
      // make sure this is on a device, not an emulation (e.g. chrome tools device mode)
      if (!this.platform.is('cordova')) {
        return false;
      }

      if (this.platform.is('ios')) {
        this.storageDirectory = cordova.file.documentsDirectory;
      }
      else if (this.platform.is('android')) {
        this.storageDirectory = cordova.file.externalApplicationStorageDirectory;
      }
      else {
        // exit otherwise, but you could add further types here e.g. Windows
        return false;
      }
    });


  }
  toggleGroup(group) {
    if (this.isGroupShow(group)) {
      this.showGroup = null;
    } else {
      this.showGroup = group;
    }
  }

  isGroupShow(group) {
    return this.showGroup === group;
  }

  srcVideo: any;
  progress: any;
  downloadFile(archivo, urlVideo) {
    this.platform.ready().then(() => {
      const fileTransfer: TransferObject = this.transfer.create();
      fileTransfer.onProgress((progressEvent: ProgressEvent) => {
        if (progressEvent.lengthComputable) {
          this._zone.run(() => {
            let perc = Math.round(progressEvent.loaded / progressEvent.total * 100);
            this.progress = perc;
          });

        }
      });

      fileTransfer.download(urlVideo, this.storageDirectory + '/Download/' + archivo).then((entry) => {
        this.srcVideo = entry.toURL();
        const alertSuccess = this.alertCtrl.create({
          title: `Download Succeeded!`,
          subTitle: `${archivo} was successfully downloaded to: ${entry.toURL()}`,
          buttons: ['Ok']

        });

        alertSuccess.present();

      }, (error) => {

        const alertFailure = this.alertCtrl.create({
          title: `Download Failed!`,
          subTitle: `${archivo} was not successfully downloaded. Error code: ${error.code}`,
          buttons: ['Ok']
        });

        alertFailure.present();

      });

    });

  }

  setSrc(src) {
    this.events.publish('Mod', src);
  }

}



