import { Component } from '@angular/core';
import { NavController, NavParams, ActionSheetController, AlertController, Platform,App } from 'ionic-angular';
import { Transfer } from '@ionic-native/transfer';
import { File } from '@ionic-native/file';
declare var cordova: any;


@Component({
  selector: 'page-apuntes',
  templateUrl: 'apuntes.html'
})
export class ApuntesPage {
  apuntes: any;
  contenido: any = "<h1>Apuntando para probar la descargada de los files</h1>";
  storageDirectory: string = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, public actionSheetCtrl: ActionSheetController, public platform: Platform, public transfer: Transfer, public alertCtrl: AlertController, public file: File,public app:App) {
    this.platform.ready().then(() => {
      if (!this.platform.is('cordova')) {
        return false;
      }
      if (this.platform.is('ios')) {
        this.storageDirectory = cordova.file.documentsDirectory;
      }
      else if (this.platform.is('android')) {
        this.storageDirectory = cordova.file.externalApplicationStorageDirectory;;
      }
      else {
        // exit otherwise, but you could add further types here e.g. Windows
        return false;
      }
    });

  }

  presentActionSheet(i) {
    let actionSheet = this.actionSheetCtrl.create({
      buttons: [
        {
          text: 'Editar',
          role: 'editar',
          handler: () => {
            console.log('Editar clicked');
            this.contenido = "Apuntando";
          }
        }, {
          text: 'Descargar',
          handler: () => {
            console.log('Descargar clicked');
            this.downloadFile('prueba.html', this.contenido);
          }
        }, {
          text: 'Eliminar',
          role: 'eliminar',
          handler: () => {
            console.log('Eliminar clicked');
            let confirm = this.alertCtrl.create({
              message: '¿Está seguro que desa eliminar el apunte?',
              buttons: ['Cancelar',

                {
                  text: 'Eliminar',
                  handler: () => {
                    this.deleteApunte(i);
                  }
                }
              ]
            });
            confirm.present();

          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  deleteApunte(idx) {
    this.apuntes.splice(idx, 1);
  }

  //Function usada para descargar el contenido del apunte crea el file recibe como parametro el nombre del archivo + su extension y el contenido del apunte
  downloadFile(archivo, contenido) {
    this.file.writeFile(this.storageDirectory, archivo, contenido, { replace: true })
      .then((entry) => {
        const alertSuccess = this.alertCtrl.create({
          title: `Download Succeeded!`,
          subTitle: `${archivo} was successfully downloaded to: ${entry.toURL()}`,
          buttons: ['Ok']

        });

        alertSuccess.present();

      })
      .catch((err) => {
        const alertFailure = this.alertCtrl.create({
          title: `Download Failed!`,
          subTitle: `${archivo} was not successfully downloaded. Error code: ${err.code}`,
          buttons: ['Ok']
        });
        alertFailure.present();
      });
  }
  add() {
    this.contenido = "";
  }
  apuntesEdit(){
   
  }
}



