import { Component } from '@angular/core';
import { NavController, App, NavParams, Events } from 'ionic-angular';
import { ModulesPage } from "../modules/modules";
import { ScreenOrientation } from '@ionic-native/screen-orientation';

@Component({
  selector: 'page-courses',
  templateUrl: 'courses.html'
})
export class CoursesPage {
  loadProgress: any = 50;
  constructor(public navCtrl: NavController, public app: App, public navParams: NavParams, public screenOrientation: ScreenOrientation, public eventsCtrl: Events) { }


  continueCourse() {
    this.navCtrl.push(ModulesPage);
  }

  backLogin() {
    const root = this.app.getRootNav();
    root.popToRoot();
  }
  logOut() {
    localStorage.clear();
    setTimeout(() => this.backLogin(), 1000);
  }

  ionViewDidEnter() {
    this.screenOrientation.unlock();
  }
  items: string[];
  getItems(event) {
    // this.initializeItems();
    let val = event.target.value;
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

}



