import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Platform } from 'ionic-angular';
import { ContributionsAnswerPage } from "../contributions/answers/answers";
import { Transfer, TransferObject } from '@ionic-native/transfer';
import { File } from '@ionic-native/file';
import { FileChooser } from '@ionic-native/file-chooser';
import { LearnService } from "../../app/learn.service";
import { Diagnostic } from '@ionic-native/diagnostic';

declare var cordova: any;
@Component({
  selector: 'page-contributions',
  templateUrl: 'contributions.html'
})
export class ContributionsPage {
  contenido: any;
  fileContribution: any;
  storageDirectory: string = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController, public platform: Platform, public transfer: Transfer, public file: File, public fileChooser: FileChooser, public service: LearnService, public diagnostic: Diagnostic) {
   //Utilizado para obtener la ruta donde se va a descargar el file dependiendo de la plataforma
    this.platform.ready().then(() => {
      // make sure this is on a device, not an emulation (e.g. chrome tools device mode)
      if (!this.platform.is('cordova')) {
        return false;
      }

      if (this.platform.is('ios')) {
        this.storageDirectory = cordova.file.documentsDirectory;
      }
      else if (this.platform.is('android')) {
        this.storageDirectory = cordova.file.externalApplicationStorageDirectory;
        // this.storageDirectory = cordova.file.externalRootDirectory;

        // this.file.externalRootDirectory
      }
      else {
        // exit otherwise, but you could add further types here e.g. Windows
        return false;
      }
    });

  }

  deleteContribution(idx) {
    let confirm = this.alertCtrl.create({
      message: '¿Está seguro que desa eliminar la contribucion?',
      buttons: ['Cancelar',

        {
          text: 'Eliminar',
          handler: () => {
          }
        }
      ]
    });
    confirm.present();
  }
  editContribution(idx, contribution) {
    this.contenido = "Aportando";
  }

  answers() {
    this.navCtrl.push(ContributionsAnswerPage);
  }

  setVote(id, tipo, estado) {
  }

  // uploadFile() {
  //   this.fileChooser.open().then(url => {
  //     alert(url)
  //     const fileTransfer: TransferObject = this.transfer.create();
  //     let options: FileUploadOptions = {
  //       fileKey: 'ionicfile',
  //       fileName: "ionicfile.jpg",
  //       chunkedMode: false,
  //       mimeType: "image/jpeg",
  //       headers: {}
  //     }
  //     fileTransfer.upload(url, encodeURI("http://192.168.0.18/upload/upload.php"), options)
  //       .then((data) => {
  //         //        success
  //         alert("success" + JSON.stringify(data));
  //       }, (err) => {
  //         // error
  //         alert("error" + JSON.stringify(err));
  //       })

  //   })
  //     .catch(e => console.log(e));

  // }

  //Obtiene el file y lo asigna a la variable  fileAporte
  fileAporte: any
  uploadFile(file) {
    this.fileAporte = file;
  }
  data = { "contenido": "", "file": "", "ext": "" };

  //Agrega el aporte 
  add() {
    this.data.contenido = this.contenido;
    if(this.fileAporte!=undefined){
      this.data.ext = this.fileAporte.name.substring(this.fileAporte.name.indexOf(".") + 1, this.fileAporte.name.length);
      this.data.file = this.fileAporte;
     
    }else{
      console.log("Vacio");
    }
    this.service.newPost(this.data);
  }

  //Descarga el archivo que tenga la contribution pero primero pide la autorizacion del usuario de acceder a la multimedia del dispositivo
  //Revisa primero si existe la carpeta de descargas en el dispositivos si esto es aspi ejecuta la funcion download file la cual recibe como parametros la url de donde se va a descargar el archivo y la ubicacion donde se va a descargar el archivo en este caso la carpeta de descargas  y el nombre que se le va a dar al file si todo esto es exitoso deberia descargar el archivo en la carpeta de descargas sino ejecuta un error diciendo que el file a fallado
  downloadFile(url, fileName) {
    this.diagnostic.requestExternalStorageAuthorization().then(() => {
        this.diagnostic.isExternalStorageAuthorized().then((isAvailable) => {
            this.file.checkDir(this.file.externalRootDirectory, "download")
              .then((success) => {
                this.storageDirectory = this.file.externalRootDirectory + "download/";
                const fileTransfer: TransferObject = this.transfer.create();
                fileTransfer.download(url, encodeURI(this.storageDirectory + fileName), true).then((entry) => {

                  const alertSuccess = this.alertCtrl.create({
                    title: `Download Succeeded!`,
                    subTitle: `${fileName} was successfully downloaded to: ${entry.toURL()}`,
                    buttons: ['Ok']

                  });

                  alertSuccess.present();

                }, (error) => {
                        const alertFailure = this.alertCtrl.create({
                          title: `Download Failed!`,
                          subTitle: `${fileName} was not successfully downloaded. Error code: ${error.code}`,
                          buttons: ['Ok']
                        });
                        alertFailure.present();
                });
              }).catch((error) => {
                // this.storageDirectory = this.file.dataDirectory;
                // alert("c error storage dir = ");
                // alert(this.storageDirectory);
                // alert(error);
              }
              )
          }, (error) => {
            alert("No write Permission");
            alert(error);
            return false;
          }).catch((error) => {
            alert('Error getting write authorization');
            alert(error);
            return false;
          });
      });
  }

}



