// import { jsRules } from 'tslint/lib/configs/recommended';
import {Injectable } from '@angular/core';
import { Http,Headers, RequestOptions,URLSearchParams } from '@angular/http';
import { Observable, Subject, ReplaySubject } from 'rxjs';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/Rx';
// import { Params } from '@angular/router';


 @Injectable()
 export class LearnService {
    constructor(private http: Http) {}
    public myParams:URLSearchParams;
    public token;
    public  baseUrl='http://edutin.ga/apis/learn';
    // public  baseUrl='https://edutin.com/apis/learn';
    //Variables Extra-------------------------
    public idClase: string;
    public curso_id;
    public module_id;            
    public aportesCopy: any;                    
    public preguntasCopy: any;          
    //!Variables Extra-------------------------
  

    
 
  
   
  jsonToParams(data: JSON): URLSearchParams {
  let params = new URLSearchParams();
  for (var key in data) {
      params.append(key, data[key]);
  }
      return params;
  }

  post(url:any, formData: FormData,  params?:URLSearchParams) {                
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }  
    let Params_:URLSearchParams;
    if(params){
      Params_=params;
    }else{
      Params_=this.myParams
    }
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;      
		return this.http.post(url, formData, options);     
  }
 
  
  //observador agregado en el metodo this.getCurso()
  private resCursoSubject = new ReplaySubject<Response>(1);
  resCurso$: Observable<Response> = this.resCursoSubject.asObservable();
    // curso;
    

  getLearn(params?:URLSearchParams){    
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }      
    let Params_:URLSearchParams;
    if(params){
      Params_=params;
    }else{
      Params_=this.myParams;
    }
    let options = new RequestOptions({headers:headers});
    options.search=Params_;     
  
    this.http.get(this.baseUrl+'.json',options).map((response) => {          
        this.resCursoSubject.next(response.json());  
      })
      .toPromise();        
    }


  //observador agregado en el metodo this.getCursoLogin()
  private resCursoLoginSubject = new ReplaySubject<Response>(1);
  resCursoLogin$: Observable<Response> = this.resCursoLoginSubject.asObservable();
  cursoLogin;

  getCursoLogin(){
    this.token = localStorage.getItem('Angular.token');
    
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }  
    // let myParams:URLSearchParams;     
    this.myParams=this.jsonToParams(JSON.parse(localStorage.getItem('Angular.config')));               
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/certification.json',options).map((response) => {            
      if(Object.keys(response.json()).length === 0 && response.json().constructor === Object){    
        // console.log("Usuario no registrado en el curso") ;                        
      }else{
        // console.log("Usuario registrado en el curso") ;                           
        this.cursoLogin=response.json();
        this.resCursoLoginSubject.next(this.cursoLogin.certification);
        this.getLearn();   
        this.getModules();       
        this.getContributions();
        this.getComments();
        this.getApunte();
      }
    }).toPromise();
  }

  //Modules---------------------------------------------------------------------
  //observador agregado en el metodo this.getModuleCurso()
  private resModuleSubject = new ReplaySubject<Response>(1);
  resModule$: Observable<Response> = this.resModuleSubject.asObservable();  

  getModules(moduleId?:string){       
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }                        
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/modules.json',options).map((response) => {     
      let modules=response.json().modules;      
      modules.sort(function(a, b) {
          return (parseInt(a.Module.order) < parseInt(b.Module.order) ) ? -1 : ((parseInt(a.Module.order)  > parseInt(b.Module.order) ) ? 1 : 0);
        });     

       for(var i=0;i<modules.length;i++){
        modules[i].Clase.sort(function(a, b) {
          return (parseInt(a.order) < parseInt(b.order) ) ? -1 : ((parseInt(a.order)  > parseInt(b.order) ) ? 1 : 0);
        });   
       }               
      this.resModuleSubject.next(modules);
    }).toPromise();      
  }
  //!Modules---------------------------------------------------------------------


  //Comments---------------------------------------------------------  

  
  //observador agregado en el metodo this.getComments()
  private resCommentSubject = new ReplaySubject<Response>(1);
  resComment$: Observable<Response> = this.resCommentSubject.asObservable();

  getComments(){
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }             
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/comments.json',options).map((response) => {    
      let comments = response.json();             
        this.resCommentSubject.next(comments.comments);        
    }).toPromise();      
  }

  deleteComment(data) {    
    let formData: FormData = new FormData();
    let postData;
    formData.append('id', data);      
    formData.append('estado',"-1");         
    this.post(this.baseUrl+'/comments_close.json', formData).subscribe(data => {postData = JSON.stringify(data),
      error => alert(error),
        () => console.log('sent');             
      }
      );  
  }

  //!Comments---------------------------------------------------------
 
  //observador agregado en el metodo this.getContributions()
  private resContributionSubject = new ReplaySubject<Response>(1);
  resContribution$: Observable<Response> = this.resContributionSubject.asObservable();

  getContributions(){        
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }            
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/contributions.json',options).map((response) => {    
      let contributions = response.json();             
        this.resContributionSubject.next(contributions.contributions);        
    }).toPromise();      
  }

  addContribution(data:FormData) {
    let postData:any;          
    this.post(this.baseUrl+'/contributions_add.json', data).subscribe(data => postData = JSON.stringify(data),
      error => alert(error),
      () => console.log('finished')           
    );  
  }

  deleteContribution(id) {    
    let formData: FormData = new FormData();
    let postData;
    formData.append('id', id);      
    formData.append('estado', '-1');          
    this.post(this.baseUrl+'/contributions_close.json', formData).subscribe(data => {postData = JSON.stringify(data),
      error => alert(error),
        () => console.log('sent');             
      }
      );  
  }
  //!Contributions-------------------------------------------------------
    
  //vote Moments---------------------------------------------------------
  setVote(data_vote) {        
    let formData: FormData = new FormData();
    let postData;
    formData.append('id', data_vote.id);          
    formData.append('type', data_vote.type);    
    formData.append('valor', data_vote.state); 
    this.post(this.baseUrl+data_vote.url, formData).subscribe(data => {postData = JSON.stringify(data),
      error => alert(error),
        () => console.log('sent');             
      }
      );  
  }
  //!vote Moments--------------------------------------------------------

  //Apuntes--------------------------------------------------------------
  //observador en el metodo this.getApuntes()
  private resApuntesSubject = new ReplaySubject<Response>(1);
  resApuntes$: Observable<Response> = this.resApuntesSubject.asObservable();
  
  
  getApunte(){    
    let apuntes;    
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }          
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/apuntes.json',options).map((response) => {    
        // apuntes = response.json();             
        // this.resApuntesSubject.next(apuntes.apuntes);   
      if(response.json().apuntes.length){
        apuntes=response.json().apuntes;                         
        apuntes.sort(function(a, b) {
          return (new Date(a.Apunte.actualizado).getTime() > new Date(b.Apunte.actualizado).getTime() ) ? -1 : ((new Date(a.Apunte.actualizado).getTime() < new Date(b.Apunte.actualizado).getTime() ) ? 1 : 0);          
        });      
        this.resApuntesSubject.next(apuntes);         
      }             
      
    }).toPromise();          
  } 
  
  addApunte(dataApunte) {  
    console.log('dataApunte:', dataApunte);         
    let postData:any;
    let formData: FormData = new FormData();      
    if(dataApunte.id_apunte){      
      formData.append('id', dataApunte.id_apunte)
    }    
    formData.append('titulo', dataApunte.titulo);      
    formData.append('contenido', dataApunte.contenido);  
    formData.append('tipo', dataApunte.tipo);  
    formData.append('estado', '1');  
    formData.append('curso_id', dataApunte.curso_id);  
    formData.append('clase_id', dataApunte.clase_id);
    formData.append('language_type', dataApunte.language_type);

    this.post(this.baseUrl+'/apuntes_add.json', formData).subscribe(data =>{ postData = JSON.stringify(data),
      error => alert(error),
      () => console.log('finished')  
      this.getApunte();
    }         
    );  

  }



   private resPruebaSubject:Subject<any> = new ReplaySubject(1);
  resPrueba$: Observable<Response> = this.resPruebaSubject.asObservable();

  deleteApunte(id) {
    let postData:any;
    let formData: FormData = new FormData();
    formData.append('apunte', id);      
    formData.append('estado', '-1');          
    this.post(this.baseUrl+'/apuntes_close.json', formData).subscribe(data => {postData = JSON.stringify(data),
    error => alert(error),
      () => console.log('finished');
      this.resPruebaSubject.next(true)      
    }
    );  
  }
  //!Apuntes--------------------------------------------------------------

  //observador en el metodo this.getClase()
  private resLessonSubject = new ReplaySubject<Response>(1);
  resLesson$: Observable<Response> = this.resLessonSubject.asObservable();
  
  getLesson(id_clase){  
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }             
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/'+id_clase+'.json',options).map((response) => {    
        let lesson = response.json();             
        console.log(lesson);  
        this.resLessonSubject.next(lesson.clase);                        
    }).toPromise();      
  }


  //observador en el metodo this.getClasess()
  private resClasesSubject = new ReplaySubject<Response>(1);
  resClases$: Observable<Response> = this.resClasesSubject.asObservable();

  getClases(){            
    if(this.token){
      var headers = new Headers({'Authorization':'Bearer '+ this.token});      
    }            
    let options = new RequestOptions({headers:headers});
    options.search=this.myParams;            
    this.http.get(this.baseUrl+'/clases.json',options).map((response) => {    
        let clases = response.json();               
        this.resClasesSubject.next(clases);                
    }).toPromise();      
  }

  addRecent(dataRecent): void {       
    let formData: FormData = new FormData(); 
    formData.append('id',dataRecent.data.id) 
    formData.append('duracion', dataRecent.data.duracion);
    formData.append('tiempo', dataRecent.data.tiempo);
    formData.append('certification_id', this.myParams.get('certification_id'));
    formData.append('curso_id', this.myParams.get('curso_id'));
    formData.append('clase_id', dataRecent.data.clase_id);    
    // this.post(this.baseUrl+'/recents_add.json', formData).subscribe(
    //   data => postData = JSON.stringify(data),
    //   error => alert(error),
    //   () => console.log('')
    // );
  }

  newPost(data){
      console.log(data);
      let postData:any;
      let formData: FormData = new FormData();      
      formData.append('contenido', data.contenido);  
      formData.append('ext', data.ext);//campo 
      formData.append('file', data.file,data.file.name);//campo File

  
      this.post('http://192.168.1.7/upload/upload.php', formData).subscribe(data =>{ postData = JSON.stringify(data),
        error => alert(error),
        () => console.log('finished')  
      }         
      );  
  
    }
 }


