import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { HttpModule } from '@angular/http';
import { MyApp } from './app.component';
import { WelcomePage } from "../pages/welcome/welcome";
import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/signup/signup';
import { RecoverPage } from "../pages/recover/recover";
import { CoursesPage } from "../pages/courses/courses";
import { ApuntesPage } from '../pages/apuntes/apuntes';
import { CommentsPage } from '../pages/comments/comments';
import { CommentsAnswerPage } from "../pages/comments/answers/answers";
import { ContributionsPage } from '../pages/contributions/contributions';
import { ContributionsAnswerPage } from "../pages/contributions/answers/answers";
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LearnService } from './learn.service';
import { VideoLocalPage } from "../pages/video/local";
import { ModulesPage } from "../pages/modules/modules";
// Videogular
import {VgCoreModule} from 'videogular2/core';
import {VgControlsModule} from 'videogular2/controls';
import {VgOverlayPlayModule} from 'videogular2/overlay-play';
import {VgBufferingModule} from 'videogular2/buffering';
import { AuthService } from '../providers/auth-service/auth-service';
import { ProgressBarComponent } from '../components/progress-bar/progress-bar';
// Videogular

//Plugins
import { Transfer, TransferObject } from '@ionic-native/transfer';
import { File } from '@ionic-native/file';
import { FileChooser } from '@ionic-native/file-chooser';
import { Diagnostic } from '@ionic-native/diagnostic';


@NgModule({
  declarations: [
    MyApp,
    WelcomePage,
    LoginPage,
    SignUpPage,
    RecoverPage,
    CoursesPage,
    VideoLocalPage,
    ModulesPage,
    ApuntesPage,
    CommentsPage,
    CommentsAnswerPage,
    ContributionsPage,
    ContributionsAnswerPage,
    ProgressBarComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp),
    VgCoreModule,
    VgControlsModule,
    VgOverlayPlayModule,
    VgBufferingModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    WelcomePage,
    LoginPage,
    SignUpPage,
    RecoverPage,
    CoursesPage,
    VideoLocalPage,
    ModulesPage,
    ApuntesPage,
    CommentsPage,
    CommentsAnswerPage,
    ContributionsPage,
    ContributionsAnswerPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LearnService,
    AuthService,
    ScreenOrientation,
    Transfer, TransferObject, File, FileChooser, Diagnostic
  ]
})
export class AppModule {}



